/* 
loading
self.GLOBAL.loadingShow();
self.GLOBAL.loadingHide();
*/
var loadingId = null;
var loadingShow = function(options) {
  setTimeout(_ => {
    var vm = window.vm;
    loadingId = vm.$createToast({
      time: 0,
      txt: (options && options.message) || "",
      mask: true
    });
    loadingId.show();
  });
};
var loadingHide = function() {
  if (loadingId) {
    loadingId.hide();
  }
};

/*
toast
this.GLOBAL.toast({option})
option

this.GLOBAL.toast({
  type: 'correct',
  message: '',
  time:1500
});
typeloadingcorrecterrorwarntxt
*/
var g_toast = null;
var toast = function(options) {
  setTimeout(() => {
    if (!g_toast) {
      var self = this;
      var vm = window.vm;
      g_toast = vm.$createToast({
        txt: (options && options.message) || "",
        type: (options && options.type) || "warn",
        time: (options && options.time) || 1200,
        mask: false
      });
      g_toast.show();
      setTimeout(_ => {
        g_toast = null;
      }, (options && options.time) || 2000);
    }
  });
};

